import axios from "axios";
import React, { useState, useEffect } from "react";
import Loader from "../Loader/Loader";

function Cards() {
  const [cardData, setCardData] = useState([]);

  useEffect(
    () =>
      axios
        .get("https://api.edyoda.com/v1/blog/")
        .then((res) => setCardData(res.data)),
    []
  );

  return (
    <div className="padding">
      {cardData.length
        ? cardData.map((data, index) => (
            <figure key={index} className="card-wrapper">
              <div className="img-section">
                <img
                  className="card-image"
                  src={data.small_image}
                  alt="logo"
                />
              </div>
              <div className="content-wrapper">
                <h4 className="heading-text">
                  {data.title.substr(0, 40)}...
                </h4>
                <div className="card-author-date-wrapper">
                  <span className="card-author"> {data.authorname} </span>
                  <span className="card-date"> | 05 Jul 2021</span>
                </div>
                <p className="content-para">
                 {data.description.substr(0, 160)}...
                </p>
              </div>
            </figure>
          ))
        : <Loader />}
    </div>
  );
}

export default Cards;
