import React from "react";

function Categories() {
  return (
    <article id="cat-wrapper" className="padding">
      <h3 className="cat-heading">Trending Post</h3>
      <p className="cat-filter">
        <i className="fas fa-filter filter-icon"></i> Filter by Category
      </p>
      <div className="cat-item-wrapper">
        <span className="active-item cat-item">All</span>
        <span className="cat-item"> Artificial Intelligence</span>
        <span className="cat-item">Cloud Computing</span>
        <span className="cat-item">DevOps</span>
        <span className="cat-item">Programming Languages</span>
        <span className="cat-item">Mobile Application Development</span>
        <span className="cat-item">Technology and Tools</span>
        <span className="cat-item">Get a Job in a Tech Company</span>
        <span className="cat-item">Others</span>
      </div>
    </article>
  );
}

export default Categories;
