import React from "react";

function Header() {
  return (
    <header>
    <nav>
      <div className="nav-wrapper">
        <h1 id="logo">EDYODA</h1>
        <p className="logo-story">Stories</p>
      </div>
      <p className="nav-cat">
        Explore Categories <i className="fas fa-angle-down drop-icon"></i>
      </p>
    </nav>
  </header> 
  );
}

export default Header;

